html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  font-family: "Roboto", sans-serif;
  line-height: 1.5;
  margin: 0;
}

h1 {
  font-size: 3.6rem;
  margin: 0;
}

h2 {
  font-size: 3rem;
}

h3 {
  font-size: 2.7rem;
}

h4 {
  font-size: 2.4rem;
}

a {
  text-decoration: none;
}

.contenedor {
  max-width: 120rem;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

.proyectos {
  background-color: #1D587E;
}

.nombre {
  font-weight: bold;
}

.centrar__texto {
  text-align: center;
}

.enlace__active {
  opacity: 0.5;
}

a:hover {
  opacity: 0.5;
}

.Btn-1 {
  background: linear-gradient(to bottom right, rgb(24.8285714286, 185.1265306122, 233.4214285714), rgb(19.95, 168.15, 212.8));
  color: #fff;
  display: inline-block;
  font-weight: 600;
  height: 100%;
  font-size: 1.6rem;
  text-align: center;
  letter-spacing: 0.5px;
  padding: 1.2rem 3.2rem;
  border: none;
  border-radius: 0.8rem;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.15);
  transition: all 0.3s ease;
  text-transform: uppercase;
  position: relative;
  overflow: hidden;
}
.Btn-1:hover {
  background: linear-gradient(to bottom right, rgb(20.37, 171.69, 217.28), rgb(18.9, 159.3, 201.6));
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.25);
  cursor: pointer;
}
.Btn-1::after {
  content: "";
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.15);
  transform: skewX(-20deg);
  transition: left 0.6s ease;
}
.Btn-1:hover::after {
  left: 120%;
}

.Btn-2 {
  background: linear-gradient(to bottom right, rgb(24.8285714286, 185.1265306122, 233.4214285714), rgb(19.95, 168.15, 212.8));
  color: #fff;
  display: block;
  font-weight: 600;
  height: 100%;
  font-size: 1.6rem;
  text-align: center;
  letter-spacing: 0.5px;
  padding: 1.2rem 3.2rem;
  border: none;
  border-radius: 0.8rem;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.15);
  transition: all 0.3s ease;
  text-transform: uppercase;
  position: relative;
  overflow: hidden;
}
.Btn-2:hover {
  background: linear-gradient(to bottom right, rgb(20.37, 171.69, 217.28), rgb(18.9, 159.3, 201.6));
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.25);
  cursor: pointer;
}
.Btn-2::after {
  content: "";
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.15);
  transform: skewX(-20deg);
  transition: left 0.6s ease;
}
.Btn-2:hover::after {
  left: 120%;
}
.Btn-2 {
  height: auto;
}

.headerIndice {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 1rem;
  position: relative;
  min-height: 60rem;
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.2));
}
.headerIndice .header__video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: 0; /* detrás de todo */
}
.headerIndice .header__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.2));
  z-index: 1; /* encima del video pero detrás del contenido */
}
.headerIndice .header-top,
.headerIndice .header-bottom {
  position: relative;
  z-index: 2; /* contenido encima del video y overlay */
}

.header, .headerIndice .header-top nav a, .headerIndice {
  color: #fff;
}

.logo__hummer {
  width: 15rem;
  object-fit: cover;
  border-radius: 0.5rem;
  transition: transform 0.3s ease;
}
.logo__hummer:hover {
  transform: scale(1.03);
}
@media (min-width: 992px) {
  .logo__hummer {
    width: 20rem;
  }
}
.logo__hummer {
  filter: invert(100%);
}

.header__BtnLenguague {
  display: flex;
  gap: 1rem;
}
.header__BtnLenguague:hover {
  cursor: pointer;
}
.header__BtnLenguague p {
  text-transform: uppercase;
  opacity: 0;
  display: none;
}
.header__BtnLenguague p.header__BtnLenguague--Active {
  opacity: 1;
  display: block;
}

.header-top {
  display: flex;
  align-items: center;
  flex-direction: column;
}
@media (min-width: 992px) {
  .header-top {
    flex-direction: row;
    justify-content: space-between;
  }
}
.header-top nav {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 992px) {
  .header-top nav {
    flex-direction: row;
    justify-content: space-around;
    width: 60%;
  }
}

.header {
  background-color: #000;
  padding: 1rem;
}
.header nav a {
  color: #fff;
}

.nosotros .nosotros__texto {
  padding: 1rem;
}

.servicios {
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.2)), url("../img/2.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  min-height: 20rem;
}
.servicios .servicios__texto {
  padding: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.servicios .servicios__texto h2 {
  color: #fff;
  text-transform: uppercase;
  margin: 0;
  font-size: 4rem;
}

.servicio {
  padding: 2rem;
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
@media (min-width: 992px) {
  .servicio {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    justify-items: center;
    gap: 2rem;
    padding: 2rem 0;
  }
}
.servicio .servicio__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  gap: 1rem;
}
.servicio .servicio__item h2 {
  text-transform: uppercase;
  font-weight: 700;
  margin: 0;
  font-size: 2rem;
}
.servicio .servicio__item img {
  width: 40rem;
  height: 20rem;
  object-fit: cover;
  border-radius: 0.5rem;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.15);
  transition: transform 0.3s ease;
}
.servicio .servicio__item img:hover {
  transform: scale(1.03);
}

.proyectos {
  color: #fff;
}
.proyectos .proyectos__top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2rem;
}
.proyectos .proyectos__top h2 {
  text-transform: uppercase;
}
.proyectos .proyectos__bottom {
  display: flex;
  justify-content: space-around;
  gap: 2rem;
}
.proyectos .proyectos__bottom .proyectos__bottom--item img {
  height: 30rem;
  object-fit: cover;
  border-radius: 0.5rem;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.15);
  transition: transform 0.3s ease;
}
.proyectos .proyectos__bottom .proyectos__bottom--item img:hover {
  transform: scale(1.03);
}
.proyectos .proyectos__bottom .proyectos__bottom--item p {
  text-transform: uppercase;
  font-weight: bold;
  padding: 2rem;
}

.proyectos__bottom {
  margin-top: 2rem;
}
.proyectos__bottom .swiper-slide {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.proyectos__bottom .swiper-slide img {
  width: 100%;
  border-radius: 1rem;
  object-fit: cover;
}
.proyectos__bottom .swiper-slide p {
  margin-top: 1rem;
  text-transform: capitalize;
  font-weight: 600;
}
.proyectos__bottom .swiper-button-prev,
.proyectos__bottom .swiper-button-next {
  color: #000;
}
.proyectos__bottom .swiper-pagination-bullet-active {
  background: #000;
}

#MXNLE, #MXJAL, #MXHID, #MXMEX, #MXQUE, #MXROO {
  fill: #15B1E0;
  transition: all 0.3s ease-in-out;
  cursor: pointer;
  transform-origin: center center;
}
#MXNLE:hover, #MXJAL:hover, #MXHID:hover, #MXMEX:hover, #MXQUE:hover, #MXROO:hover {
  fill: #15B1E0;
  filter: drop-shadow(0 0 8px rgba(0, 255, 255, 0.6));
  transform: scale(1.05);
}

path {
  pointer-events: visibleFill;
}

.Zonas {
  padding: 1rem;
  display: flex;
  flex-direction: column-reverse;
  align-items: center;
  margin-top: 4rem;
}
@media (min-width: 992px) {
  .Zonas {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    place-items: start; /* los demás se quedan arriba a la izquierda */
  }
}

@media (min-width: 992px) {
  .Zonas__info {
    justify-self: center; /* centra horizontalmente */
    align-self: center; /* centra verticalmente */
  }
}
.Zonas__info ul li {
  white-space: nowrap;
}

.Zona__mapa {
  width: 100%;
  height: 100%;
}
@media (min-width: 992px) {
  .Zona__mapa {
    height: auto;
    width: 70rem;
  }
}

.Zona__titulo {
  text-transform: uppercase;
  color: #15B1E0;
}
.Zona__titulo h3 {
  font-weight: bold;
  font-size: 4rem;
  margin: 0;
}

.footer {
  display: flex;
  padding: 1rem;
  align-items: center;
  flex-direction: column;
  background-color: #000;
}
@media (min-width: 992px) {
  .footer {
    flex-direction: row;
    justify-content: space-evenly;
  }
}
.footer .logo {
  width: 15rem;
  filter: invert(100%);
  height: 10rem;
}
.footer .footer__Texto {
  color: #fff;
}
.footer .footer__Texto .footer__Texto--top p {
  text-align: center;
  color: #1D587E;
  font-weight: bold;
}
.footer .footer__Texto .footer__Texto--bottom {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
@media (min-width: 992px) {
  .footer .footer__Texto .footer__Texto--bottom {
    flex-direction: row;
  }
}
.footer .footer__Texto .footer__Texto--bottom div p span {
  font-weight: bold;
}

.nosotros__servicios .nosotros__servicios--item {
  display: flex;
  flex-direction: column;
  padding: 2rem;
  margin: 4rem 0;
  align-items: center;
  object-fit: cover;
  border-radius: 0.5rem;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.15);
  transition: transform 0.3s ease;
}
.nosotros__servicios .nosotros__servicios--item:hover {
  transform: scale(1.03);
}
.nosotros__servicios .nosotros__servicios--item img {
  width: 4.3rem;
  height: 4.3rem;
  filter: brightness(0);
}
.nosotros__servicios .nosotros__servicios--item .nosotros__servicios--item__texto {
  text-align: center;
}
.nosotros__servicios .nosotros__servicios--item .nosotros__servicios--item__texto p {
  font-size: 3rem;
  padding: 0;
  margin: 0;
}
.nosotros__servicios .nosotros__servicios--item .nosotros__servicios--item__texto h4 {
  margin: 0;
  font-weight: 300;
  font-size: 1.5rem;
  color: #b0a8a8;
}

.swiper-servicios .swiper-wrapper {
  box-sizing: border-box !important;
}

.swiper-servicios {
  width: 100%;
  overflow: hidden;
  padding: 0 1rem; /* Espacio a los lados para móviles */
  box-sizing: border-box;
}

/* Opcional: por si tu .contenedor tiene display:flex */
.nosotros__servicios.contenedor {
  display: block;
}

/* /////////////////////////// */
@media (max-width: 768px) {
  .swiper-servicios .swiper-slide {
    width: auto;
  }
}
.servicios-page .servicios {
  text-align: center;
  padding: 2rem;
}
.servicios-page .servicios h1 {
  font-size: 2.8rem;
  margin-bottom: 40px;
  color: #fff;
}
.servicios-page .servicios .servicios-grid {
