  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 30px;
}
.servicios-page .servicios .servicios-grid .servicio-card {
  background-color: #fff;
  padding: 30px;
  border-radius: 15px;
  box-shadow: 0 6px 15px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s, box-shadow 0.3s;
}
.servicios-page .servicios .servicios-grid .servicio-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.2);
}
.servicios-page .servicios .servicios-grid .servicio-card h3 {
  font-size: 1.5rem;
  margin-bottom: 15px;
  color: #111;
}
.servicios-page .servicios .servicios-grid .servicio-card p {
  font-size: 1rem;
  color: #666;
  line-height: 1.5;
}
.servicios-page .cotizacion {
  margin-top: 80px;
  text-align: center;
  margin-bottom: 8rem;
}
.servicios-page .cotizacion h2 {
  font-size: 2.5rem;
  margin-bottom: 40px;
  color: #111;
}
.servicios-page .cotizacion .form-cotizacion {
  max-width: 600px;
  margin: 0 auto;
  background-color: #fff;
  padding: 40px;
  border-radius: 15px;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.15);
}
.servicios-page .cotizacion .form-cotizacion .form-group {
  margin-bottom: 25px;
  text-align: left;
}
.servicios-page .cotizacion .form-cotizacion .form-group label {
  display: block;
  margin-bottom: 8px;
  font-weight: bold;
  color: #333;
}
.servicios-page .cotizacion .form-cotizacion .form-group input,
.servicios-page .cotizacion .form-cotizacion .form-group select,
.servicios-page .cotizacion .form-cotizacion .form-group textarea {
  width: 100%;
  padding: 12px 15px;
  border: 1px solid #ccc;
  border-radius: 8px;
  font-size: 1rem;
  transition: border 0.3s, box-shadow 0.3s;
}
.servicios-page .cotizacion .form-cotizacion .form-group input:focus,
.servicios-page .cotizacion .form-cotizacion .form-group select:focus,
.servicios-page .cotizacion .form-cotizacion .form-group textarea:focus {
